import Vue from 'vue';
import vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
Vue.use(vuex);

const vuexLocal = new VuexPersistence({
    storage: window.localStorage,
    key:'videoMobileDarkStore'
});
export default new vuex.Store({
    strict: true,
    state:{
        /**/
        isLogin:false,
        /*(0 1 2 )*/
        mode:2,
        /*()*/
        userInfo:null,
        /**/
        showTabbar:true,
        /**/
        searchHistory:[],
        /**/
        imeiCode:null,
        /**/
        userName:null,
        /**/
        password:null,
        /**/
        searchInfoSave:{
            /*type:
            * 1.info  
            * 2.id infoid
            * 3.videoType:1- 2- 3- 4-G() 
            * 4.sortFn: null
            * */
            type:1,
            info:"",
            videoType:1,
            sortFn:null
        },
        /**/
        alipayAccount:null,
        /**/
        alipayName:null,
        bankAccount:null,
        /**/
        nowGetCashCount:0,
        /*id*/
        agentId:null,
        /**/
        configInfo:null,
        /**/
        videoHistoryList:[],
        /**/
        shareList:[],
        /**/
        adList:[],
        /**/
        downloadUrl:{
            androidDownUrl:null,
            IOSDownUrl:null
        },
        /**/
        scrollPosition:0
    },
    getters:{

    },
    mutations:{
        /**/
        changeScrollPos(state,val){
            state.scrollPosition=val;
        },
        /**/
        changeDownloadUrl(state,obj){
            state.downloadUrl.androidDownUrl=obj.androidDownUrl;
            state.downloadUrl.IOSDownUrl=obj.IOSDownUrl;
        },
        /**/
        changeAdList(state,val){
            state.adList=val;
        },
        /**/
        saveShareList(state,val){
            state.shareList=val;
        },
        /**/
        changeVideoHistory(state,val){
            state.videoHistoryList=val;
        },
        /**/
        configInfoSave(state,val){
            state.configInfo=val;
        },
        /**/
        changeUserAccount(state,val){
            state.userName=val.userName;
            state.password=val.password;
        },
        /**/
        changeImeiCode(state,val){
            state.imeiCode=val;
        },
        /*id*/
        saveAgentID(state,val){
            state.agentId=val;
        },
        /**/
        changeSearchHistory(state,obj){
            /*
            * obj:{
            *   type:1  1: 0.
            *   val:""   
            * }
            * */
            if(obj.type===1&&obj.val===""){
                return;
            }
            if(obj.type===1){
                state.searchHistory.indexOf(obj.val)===-1?state.searchHistory.push(obj.val):null;
            }
            else if(obj.type===0){
                state.searchHistory=[];
            }

        },
        /**/
        changeUserInfoOneKey(state,obj){
            /*
            * obj:{key:'',val:''}
            * */
            state.userInfo[obj.key]=obj.val;
        },
        /**/
        changeBankAccount(state,val){
            state.bankAccount=val;
        },
        /**/
        changeSearchInfo(state,val){
            state.searchInfoSave=val;
        },
        /**/
        changeBottomTabbar(state,val){
            state.showTabbar=val;
        },
        /**/
        changeLoginStatus(state,val){
            state.isLogin=val;
        },
        /**/
        userInfoSave(state,val){
            state.userInfo=val;
        },
        /*token*/
        changeUserToken(state,val){
            state.userInfo.token=val;
        }
    },
    actions:{

    },
    plugins: [vuexLocal.plugin]
});



