<template>
    <div class="publicAllContentBg">
        <!--banner-->
        <van-swipe class="indexSwiper" id="publicIndexBanner" indicator-color="white">
            <van-swipe-item v-for="(item,index) in indexBannerConfig.list" :key="index">
                <aesImage :file-url="item.imgUrl" :primordial-img="false"
                          img-fit-type="fill"></aesImage>
            </van-swipe-item>
        </van-swipe>
        <!---->
        <!--                        <publicScrollVideoList @goMore="lookForMoreFn(null,0)" :video-list="indexVideoModule[0].videoList"></publicScrollVideoList>-->
        <!---->
        <indexVideoModule
            v-for="(item,index) in indexVideoModule"
            :key="index"
            :module-info="item.info"
            :video-list="item.videoList"
            :lazy-render="false"
            @changeAnother="changeAnotherBatch(item,index)"
            @lookForMore="lookForMoreFn(item,index)"
        ></indexVideoModule>
    </div>

</template>

<script>
import indexVideoModule from "../../components/indexVideoModule";
import aesImage from "../../components/aesImage";
import publicScrollVideoList from "../../components/publicScrollVideoList";
export default {
    name: "index",
    components:{
        indexVideoModule,
        aesImage,
        publicScrollVideoList
    },
    data(){
        return {
            /*banner*/
            indexBannerConfig: {
                /**/
                list: []
            },
            /**/
            indexVideoModule: [
                {
                    info: {
                        name: "",
                        info: '',
                        icon: require('../../assets/icon_free_nor.png')
                    },
                    /**/
                    page: 1,
                    /**/
                    videoList: [],
                },
                {
                    info: {
                        name: "",
                        info: '',
                        icon: require('../../assets/icon_blockbuster_nor.png')
                    },
                    /**/
                    page: 1,
                    /**/
                    videoList: [],
                },
                {
                    info: {
                        name: "",
                        info: '',
                        icon: require('../../assets/icon_sentiment_nor.png')
                    },
                    /**/
                    page: 1,
                    /**/
                    videoList: [],
                }
            ]
        }
    },
    methods:{
        /*(1)*/
        getFreeVideoFn(page, type) {
            /**/
            this.$requestApi.getVideoList({
                length: 4,
                page: page,
                orderType: 10000,
                type: 2
            })
                .then(res => {
                    if (res.data.list.length === 0 && type === 1) {
                        /*this.$message({
                            message: '',
                            type: 'warning'
                        });*/
                        this.getFreeVideoFn(1);
                        return;
                    }
                    /**/
                    this.indexVideoModule[0].videoList = res.data.list;
                    /**/
                    this.indexVideoModule[0].page = res.data.page;
                })
                .catch(err => {

                })
        },
        /*(2)*/
        getRankVideoFn(page, type) {
            this.$requestApi.getVideoList({
                length: 4,
                page: page,
                type: 3,
                orderType: 10001,
                orderText: [{
                    "dir": "desc",
                    "column": "addTimeStamp"
                }]
            })
                .then(res => {
                    if (res.data.list.length === 0 && type === 1) {
                        // this.$toast.fail('');
                        this.getRankVideoFn(1, 1);
                        return;
                    }
                    /**/
                    this.indexVideoModule[1].videoList = res.data.list;
                    /**/
                    this.indexVideoModule[1].page = res.data.page;
                })
                .catch(err => {

                })
        },
        /*(3)*/
        getHotRankVideoFn(page, type) {
            console.log(page);
            this.$requestApi.getVideoList({
                length: 4,
                page: page,
                type: 1,
                orderType: 10002,
                orderText: [{
                    "dir": "desc",
                    "column": "seeCount"
                }]
            })
                .then(res => {
                    if (res.data.list.length === 0 && type === 1) {
                        /*this.$toast.fail('');*/
                        this.getHotRankVideoFn(1, 1);
                        return;
                    }
                    /**/
                    this.indexVideoModule[2].videoList = res.data.list;
                    /**/
                    this.indexVideoModule[2].page = res.data.page;
                })
                .catch(err => {

                })
        },
        /*banner*/
        getBanner() {
            /*banner*/
            this.$requestApi.getIndexBanner({
                type: 1
            })
                .then(res => {
                    this.indexBannerConfig.list = res.data.list;
                })
                .catch(err => {
                });
        },
        /**/
        lookForMoreFn(item, index) {
            console.log('');
            console.log(item);
            switch (index) {
                case 0:
                    /**/
                    this.$store.commit('changeSearchInfo', {
                        type: 1,
                        info: "",
                        videoType: 2,
                        sortFn:null
                    });
                    this.$router.push({name: "searchResult"});
                    break;
                case 1:
                    /**/
                    /*this.$globalFn.publicOpenNewWindow('/search', {
                        keyword: "",
                        sortType: 0,
                        videoType: 3
                    });*/
                    this.$store.commit('changeSearchInfo', {
                        type: 1,
                        info: "",
                        videoType: 3,
                        sortFn: {
                            dir: "desc",
                            column: "addTimeStamp"
                        }
                    });
                    this.$router.push({name: "searchResult"});
                    break;
                case 2:
                    /**/
                    /*this.$globalFn.publicOpenNewWindow('/search', {
                        keyword: "",
                        sortType: 2,
                        videoType: 1
                    });*/
                    this.$store.commit('changeSearchInfo', {
                        type: 1,
                        info: "",
                        videoType: 1,
                        sortFn: {
                            dir: "desc",
                            column: "seeCount"
                        }
                    });
                    this.$router.push({name: "searchResult"});
                    break;
            }
        },
        /**/
        changeAnotherBatch(item, index) {
            console.log(index);
            switch (index) {
                case 0:
                    /**/
                    this.getFreeVideoFn(this.indexVideoModule[0].page + 1, 1);
                    break;
                case 1:
                    /**/
                    this.getRankVideoFn(this.indexVideoModule[1].page + 1, 1);
                    break;
                case 2:
                    /**/
                    this.getHotRankVideoFn(this.indexVideoModule[2].page + 1, 1);
                    break;
            }
        },
    },
    created() {
        this.getBanner();
        this.getFreeVideoFn(1);
        this.getRankVideoFn(1);
        this.getHotRankVideoFn(1);
    }
}
</script>

<style scoped>

</style>



