<template>
    <div class="publicAllContentBg fullHeight login" id="login">
        <!---->
        <div class="loginButtonBox">
            <!--<div class="bottom" v-if="!showPopup">
                <van-button color="#F54B64" block @click="showPopupFn(1)"></van-button>
                <van-button class="regButton" block color="#EAF1FF" @click="showPopupFn(0)"></van-button>
            </div>-->
        </div>
        <!---->
        <van-popup v-model="showPopup"
                   closeable
                   :overlay="false"
                   round
                   @closed="$router.go(-1)"
                   position="bottom">
            <!---->
            <div class="loginFormBg" v-if="formType===0">
                <h3></h3>
                <!---->
                <div class="loginFormBox">
                    <van-field
                        v-model="registerFormConfig.userName"
                        placeholder=""
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="registerFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder=""
                        :border="false"
                    />
                    <van-field
                        v-model="registerFormConfig.passwordRepeat"
                        type="password"
                        autocomplete="off"
                        placeholder=""
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="onRegister"
                            :loading="registerFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            
                        </van-button>
                    </div>
                </div>
                <!---->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=1"></p>
                </div>
            </div>
            <!---->
            <div class="loginFormBg" v-if="formType===1">
                <h3></h3>
                <!---->
                <div class="loginFormBox">
                    <van-field
                        v-model="loginFormConfig.userName"
                        placeholder=""
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="loginFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder=""
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="onLogin(1)"
                            :loading="loginFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            
                        </van-button>
                    </div>
                </div>
                <!---->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=0"><span></span></p>
                    <p class="loginAboutTextLine" @click="formType=3"></p>
                </div>
            </div>
            <!---->
            <div class="loginFormBg" v-if="formType===2&&$store.state.isLogin">
                <h3></h3>
                <!---->
                <div class="loginFormBox">
                    <van-field
                        v-model="changePwdFormConfig.oldPwd"
                        placeholder=""
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="changePwdFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder=""
                        :border="false"
                    />
                    <van-field
                        v-model="changePwdFormConfig.passwordRepeat"
                        type="password"
                        autocomplete="off"
                        placeholder=""
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="onChangePwd"
                            :loading="changePwdFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            
                        </van-button>
                    </div>
                </div>
                <!---->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=1" v-if="!$store.state.isLogin"></p>
                </div>
            </div>
            <!---->
            <div class="loginFormBg" v-if="formType===3">
                <h3></h3>
                <!---->
                <div class="loginFormBox" v-if="formType===3">
                    <van-field
                        v-model="forgetPwdFormConfig.userName"
                        placeholder=""
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="forgetPwdFormConfig.userYzm"
                        placeholder=""
                        :border="false"
                    >
                        <template #button>
                            <van-button
                                class="sendCodeButton"
                                v-if="!forgetPwdFormConfig.yzmButtonDisable&&formType===3"
                                size="small"
                                :loading="forgetPwdFormConfig.getYzmButtonLoadingStatus"
                                @click="getYzm"
                                :disabled="forgetPwdFormConfig.yzmButtonDisable"
                            >
                            </van-button>
                            <van-button
                                class="doneSendCodeButton"
                                v-if="forgetPwdFormConfig.yzmButtonDisable&&formType===3"
                                size="small"
                                :loading="forgetPwdFormConfig.getYzmButtonLoadingStatus"
                                :disabled="true"
                            >{{ forgetPwdFormConfig.countDownNum }}s
                            </van-button>
                        </template>
                    </van-field>
                    <van-field
                        v-model="forgetPwdFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder=""
                        :border="false"
                    />
                    <van-field
                        v-model="forgetPwdFormConfig.passwordRepeat"
                        type="password"
                        autocomplete="off"
                        placeholder=""
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="forgetPwdOnSubmit"
                            :loading="forgetPwdFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            
                        </van-button>
                    </div>
                </div>
                <!---->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=0"><span></span></p>
                    <p class="loginAboutTextLine" @click="formType=1"></p>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
export default {
    name: "login",
    computed: {
        /**/
        userNameReg() {
            if (this.$gloabalInfo.regList.phone.test(this.loginFormConfig.userName) === true) {
                return true;
            }
            return false;
        },
        /**/
        userPwdReg() {
            return this.loginFormConfig.password !== "";
        }
    },
    data() {
        return {
            /**/
            showPopup: true,
            /*
            * 0
            * 1
            * 2:
            * 3:()
            * */
            formType: 1,
            /**/
            loginFormConfig: {
                /**/
                userName: "",
                /**/
                password: "",
                /*loading*/
                loading: false
            },
            /**/
            registerFormConfig: {
                /**/
                userName: "",
                /**/
                password: "",
                /**/
                passwordRepeat: "",
                /*loading*/
                loading: false
            },
            /**/
            changePwdFormConfig: {
                /**/
                oldPwd: "",
                /**/
                password: "",
                /**/
                passwordRepeat: "",
                /*loading*/
                loading: false
            },
            /**/
            forgetPwdFormConfig: {
                /**/
                userName: "",
                /**/
                password: "",
                /**/
                passwordRepeat: "",
                /**/
                userYzm: "",
                /*loading*/
                loading: false,
                /**/
                getYzmButtonLoadingStatus: false,
                /**/
                sendYzmText: "",
                /**/
                countDownNum: 60,
                /**/
                yzmButtonDisable: false
            }
        }
    },
    methods: {
        /**/
        onChangePwd(){
            /**/
            if(this.changePwdFormConfig.oldPwd===""){
                this.$toast.fail("");
                return;
            }
            if (this.changePwdFormConfig.password === "") {
                this.$toast.fail("");
                return;
            }
            if (this.changePwdFormConfig.password !== this.changePwdFormConfig.passwordRepeat) {
                this.$toast.fail("");
                return;
            }
            /*if(this.loginFormOptions.userYzm===""){
                this.$toast.fail("");
                return;
            }*/
            /**/
            this.$requestApi.changeUserPwd({
                oldPassword:this.changePwdFormConfig.oldPwd,
                newPassword:this.changePwdFormConfig.password
            })
                .then(res=>{
                    /**/
                    this.$store.commit('changeLoginStatus',false);
                    this.$store.commit('userInfoSave',null);
                    /*id*/
                    this.$store.commit('saveAgentID',null);
                    /**/
                    localStorage.removeItem('videoMobileDarkStore');
                    /*loading*/
                    this.changePwdFormConfig.loading = false;
                    /**/
                    this.$toast.success('');
                    this.formType = 1;
                })
                .catch(err=>{
                    /*this.$toast.fail(err.message);*/
                    /*loading*/
                    this.changePwdFormConfig.loading = false;
                })
        },
        /**/
        forgetPwdOnSubmit() {
            /**/
            if (this.$gloabalInfo.regList.phone.test(this.forgetPwdFormConfig.userName) === false) {
                this.$toast.fail("");
                return;
            }
            if (this.forgetPwdFormConfig.password === "") {
                this.$toast.fail("");
                return;
            }
            if (this.forgetPwdFormConfig.password !== this.forgetPwdFormConfig.passwordRepeat) {
                this.$toast.fail("");
                return;
            }
            if (this.forgetPwdFormConfig.userYzm === "") {
                this.$toast.fail("");
                return;
            }
            /**/
            this.$requestApi.resetPassCode({
                userName: this.forgetPwdFormConfig.userName,
                newPassword: this.forgetPwdFormConfig.password,
                code: this.forgetPwdFormConfig.userYzm,
                accountType: 1
            })
                .then(res => {
                    /**/
                    this.$store.commit('changeLoginStatus', false);
                    this.$store.commit('userInfoSave', null);
                    /**/
                    localStorage.removeItem('videoMobileDarkStore');
                    /*loading*/
                    this.forgetPwdFormConfig.loading = false;
                    /**/
                    this.$toast.success('');
                    this.formType = 1;
                })
                .catch(err => {
                    /*loading*/
                    this.forgetPwdFormConfig.loading = false;
                })
        },
        /**/
        sendPhoneCode() {
            this.$requestApi.sendPhoneCode({
                userName: this.forgetPwdFormConfig.userName
            })
                .then(res => {
                    this.$toast.success('');
                    /**/
                    this.codeCount();
                })
                .catch(err => {

                })
        },
        /**/
        getYzm() {
            if (this.$gloabalInfo.regList.phone.test(this.forgetPwdFormConfig.userName) === false) {
                this.$toast.fail('');
                return;
            }
            /**/
            this.sendPhoneCode();
        },
        /**/
        codeCount() {
            if (this.forgetPwdFormConfig.countDownNum === 0) {
                this.forgetPwdFormConfig.yzmButtonDisable = false;
                this.forgetPwdFormConfig.countDownNum = 60;
                window.clearTimeout(this.codeCount);
                return;
            } else {
                this.forgetPwdFormConfig.yzmButtonDisable = true;
                this.forgetPwdFormConfig.countDownNum--;
            }
            setTimeout(() => {
                this.codeCount();
            }, 1000);
        },
        /**/
        onRegister() {
            /**/
            if (this.$gloabalInfo.regList.phone.test(this.registerFormConfig.userName) === false) {
                this.$toast.fail("");
                return;
            }
            if (this.registerFormConfig.password === "") {
                this.$toast.fail("");
                return;
            }
            if (this.registerFormConfig.password !== this.registerFormConfig.passwordRepeat) {
                this.$toast.fail("");
                return;
            }
            /*loading*/
            this.registerFormConfig.loading = true;
            /**/
            this.$requestApi.register({
                userName: this.registerFormConfig.userName,
                password: this.registerFormConfig.password,
                code: "",
            })
                .then(res => {
                    /**/
                    this.onLogin(0);
                })
                .catch(err => {
                    this.$toast.fail(err.message);
                    /*loading*/
                    this.registerFormConfig.loading = false;
                })
        },
        /**/
        showPopupFn(type) {
            this.formType = type;
            /**/
            this.showPopup = true;
        },
        /**/
        onLogin(type) {
            /*
            * type01
            * */
            /**/
            if (type === 1) {
                if (!this.userNameReg) {
                    this.$toast.fail("");
                    return;
                } else if (!this.userPwdReg) {
                    this.$toast.fail("");
                    return;
                }
            }

            /**/
            this.loginFormConfig.loading = true;
            this.$requestApi.login({
                userName: type === 1 ? this.loginFormConfig.userName : this.registerFormConfig.userName,
                password: type === 1 ? this.loginFormConfig.password : this.registerFormConfig.password,
                // expire:3
            })
                .then((res) => {
                    console.log(res);
                    /**/
                    this.$toast.success('');
                    /**/
                    this.loginFormConfig.loading = false;
                    /**/
                    this.registerFormConfig.loading = false;
                    /**/
                    this.$store.commit('userInfoSave', res.data);
                    /**/
                    this.$store.commit('changeLoginStatus', true);
                    /**/
                    this.$store.commit('changeUserAccount', {
                        userName: this.loginFormConfig.userName,
                        password: this.loginFormConfig.password
                    });
                    /**/
                    this.$router.go(-1);
                })
                .catch(res => {
                    console.log(res);
                    this.$toast.fail(res.message);
                    /**/
                    this.loginFormConfig.loading = false;
                    /**/
                    this.registerFormConfig.loading = false;
                })
        },
    },
    created() {
        /**/
        console.log(this.$route.query.type);
        if(this.$route.query.type!==undefined){
            /**/
            this.showPopupFn(2);

        }
    }
}
</script>

<style scoped>
.loginButtonBox {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    background: linear-gradient(180deg, rgba(36, 42, 55, 0) 0%, rgba(23, 27, 36, 1) 100%);
}

.loginButtonBox > .bottom {
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px 40px 20px;
}

.loginFormBg {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.loginFormBg > h3 {
    color: #C2C8D7;
    font-size: 30px;
    padding-left: 20px;
}

.loginFormBox {
    margin-top: 40px;
    box-sizing: border-box;
    padding: 0 20px;
}

.bottomLine {
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
}

.bottomLine > p {
    color: #F54B64;
    font-size: 14px;
}

.buttonLine {
    margin-top: 20px;
    margin-bottom: 10px;
}
</style>



