// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
/*vant*/
import Vant from 'vant';
import 'vant/lib/index.css';
/*axios*/
import axios from 'axios'
/*vuex*/
import store from './store'
/*css*/
import './global/globalCss.css';
/*vantReset*/
import './global/resetVant.css';
/*fastclick*/
import fastClick from 'fastclick'
/*swiper*/
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
/**/
import globalTools from "./global/globalTools";
/*api*/
import api from './api/api'
/**/
import VueClipboard from 'vue-clipboard2'
/*fastClick*/
fastClick.attach(document.body)
Vue.prototype.$axios = axios;
/**/
Vue.prototype.$globalFn = globalTools.globalFunctions;
/**/
Vue.prototype.$gloabalInfo = globalTools.publicInfo;
Vue.prototype.$requestApi = api;

Vue.config.productionTip = false;
/*vant*/
Vue.use(Vant);
/*swiper*/
Vue.use(VueAwesomeSwiper);
/**/
Vue.use(VueClipboard);
/* eslint-disable no-new */
export default new Vue({
    el: '#app',
    router,
    store,
    components: {App},
    template: '<App/>'
})



