        /*store*/
        store.commit('saveAgentID', window.location.href.split('#')[0].split('=')[1]);
        console.log('id');
        console.log(window.location.href.split('#')[0].split('=')[1])
        return window.location.href.split('#')[0].split('=')[1];
        /*return window.location.href.split('#')[0].split('/')[3].split('=')[1];*/
