                &lt;!&ndash; &ndash;&gt;
                <div class="style" v-if="videoContentOptions.isCollected===1" @click="clickCollectButton(0)">
                    <div>
                        <van-icon name="star" size="20px" color="#cb266d"/>
                    </div>
                    &lt;!&ndash;<p class="active">{{videoContentOptions.collection}}</p>&ndash;&gt;
                    <p class="active"></p>
                </div>
            </li>
            <li>
                &lt;!&ndash;&ndash;&gt;
                <div class="style" @click="$toast.fail('APP')">
                    <div>
