</template>

<script>
import aesImage from "../../components/aesImage";
import guassYourLike from "../../components/guassYourLike";
import xgPlayer from "../../components/xgPlayer";

export default {
    name: 'videoContent',
    components: {
        guassYourLike,
        aesImage,
        xgPlayer
    },
    computed: {
        /*G*/
        showGPointPlayNow() {
            if (this.$store.state.userInfo.point >= this.videoContentOptions.point) {
                return true;
            }
            return false;
        },
        /**/
        showShareBg() {
            /**/
            if (this.videoStatus.isVideoPlayDone === true && this.videoStatus.isPlay === false) {
                return true;
            }
            return false;
        },
        /*Vip*/
        showNeedVipBg() {
            if (this.$store.state.isLogin === false) {
                return false;
            }
            /*vip*/
            if (this.$store.state.userInfo.isVip === 1) {
                return false;
            }
            if (this.videoContentOptions.isVip === 1) {
                /*vip*/
                return true;
            }
            return false;

        },
        /*G*/
        showNeedGPointBg() {
            if (this.videoContentOptions.isBuy === 1) {
                /*false*/
                return false;
            }
            if (this.$store.state.isLogin === true && this.$store.state.userInfo.isVip === 1 && this.videoContentOptions.point > 0) {
                /* vip G*/
                return true;
            }
            return false;
        },
        /**/
        showTryWatchPlayer() {
            /**/
            if (this.videoStatus.tryWatchVideoIsPlayDone) {
                return false;
            }
            /*,*/
            if (this.videoStatus.tryWatchVideoIsReady === true && this.videoStatus.isTryWatchVideo === true) {
                return true;
            }
            return false;
        },
        /**/
        showLoginStatus() {
            /**/
            if (this.videoContentOptions.isVip === 0 && this.videoContentOptions.point === 0) {
                // console.log('');
                return false;
            }
            /*vip*/
            if (this.$store.state.isLogin === false && this.videoContentOptions.isVip === 1) {
                // console.log('vip');
                return true;
            }
            /*g*/
            if (this.$store.state.isLogin === false && this.videoContentOptions.point > 0) {
                // console.log('g');
                return true;
            }
        }
    },
    methods: {
        /*vip*/
        goRecharge(type){
            /*
            * type1:vip 2:
            * */
            this.$router.push({name:"rechargeIndex"});
        },
        /**/
        clickShareButton() {
            if (this.$store.state.isLogin === false) {
                this.$router.push({name: "login"});
                return;
            }
            /*loading*/
            this.$toast.loading({
                duration:0,
                message:''
            });
            /**/
            this.$requestApi.getShareList({
                userId: this.$store.state.userInfo.userId,
                type: 1,
                videoId: this.$route.params.id
            })
                .then(res => {
                    console.log(res);
                    this.shareOptions.linkList.QQShortUrl = res.data.QQShortUrl;
                    this.shareOptions.linkList.webShortUrl = res.data.webShortUrl;
                    this.shareOptions.linkList.wechatShortUrl = res.data.wechatShortUrl;
                    /*loading*/
                    this.$toast.clear();
                    /**/
                    this.shareOptions.show=true;
                })
                .catch(err => {

                })
        },
        /**/
        firstStartPlay() {
            /**/
            if (this.videoPlayEnoughFn() === true) {
                /**/
                this.nowVideoStep = 2;
                return;
            }
            /**/
            this.nowVideoStep = 1;
            /**/
            // this.videoStatus.firstPlayIsStart=true;
        },
        /**/
        replayVideo() {
            /**/
            this.videoStatus.isVideoPlayDone = false;
            /**/
            this.videoStatus.isPlay = false;
            /**/
            this.nowVideoStep = 2;
        },
        /**/
        officialVideoPlayDone() {
            console.log('');
            /**/
            this.videoStatus.isVideoPlayDone = true;
            /**/
            this.videoStatus.isPlay = false;
            /**/
            this.nowVideoStep = 1;
        },
        /*G*/
        beforeGVideoPlay() {
            /*G*/
            if (this.showGPointPlayNow === true) {
                this.$dialog.confirm({
                    title: '',
                    message: '' + this.videoContentOptions.point + ""
                })
                    .then(() => {
                        /*G*/
                        if (this.$store.state.userInfo.point >= this.videoContentOptions.point) {
                            this.buyVideo();
                        } else {
                            /*G*/
                            this.$toast.fail('G');
                        }
                    })
                    .catch(() => {
                        // on cancel
                    });
            } else {
                this.$dialog.confirm({
                    title: '',
                    message: '',
                    confirmButtonText:""
                })
                    .then(() => {
                        /**/
                        this.$router.push({name:'rechargeIndex'});
                    })
                    .catch(() => {
                        // on cancel
                    });
            }

        },
        /**/
        tryWatchVideoEnded() {
            console.log('');
            /**/
            this.videoStatus.tryWatchVideoIsPlayDone = true;
            /**/
            this.nowVideoStep = 1;
        },
        /**/
        tryWatchVideoClick() {
            this.$toast.loading({
                duration: 0,
                mask: true
            });
            /**/
            this.getPreVideoUrl();
        },
        /**/
        goSearchList(item) {
            console.log(item);
            /**/
            this.$store.commit('changeSearchInfo', {
                type: 1,
                info: item,
                videoType: 1,
                sortFn: null
            });
            this.$router.push({name:'searchResult'});
            /*this.$router.push({name: "searchResultForVideoTag", params: {name: item}});*/
        },
        /**/
        videoPlayEnoughFn() {
            console.log('');
            /*vipvip,()*/
            if (this.videoContentOptions.isVip === 0 && this.videoContentOptions.point === 0) {
                console.log('vipvip,()');
                return true;
            }
            /**/
            if (this.$store.state.isLogin === true && this.videoContentOptions.isBuy === 1) {
                console.log('');
                return true;
            }
            /*vipG0*/
            if (this.$store.state.isLogin === true && this.$store.state.userInfo.isVip === 1 && this.videoContentOptions.point === 0) {
                console.log('vipG0');
                return true;
            }
            /*vipG*/
            if (this.$store.state.isLogin === true && this.$store.state.userInfo.isVip === 1 && this.$store.state.userInfo.point > this.videoContentOptions.point === 0) {
                console.log('vipvipG');
                return true;
            }
            return false;
        },
        /**/
        buyVideo() {
            /*loading*/
            this.$toast.loading({
                overlay: true,
                duration: 0,
                message: ""
            });
            this.$requestApi.useGPointBuyVideo({
                videoId: this.$route.params.id
            })
                .then(res => {
                    this.$toast.success('');
                    /**/
                    this.videoContentOptions.isBuy = 1;
                    /*G*/
                    this.$store.commit('changeUserInfoOneKey', {
                        key: "point",
                        val: res.data.point
                    });
                    /**/
                    /*this.getVideoUrl();*/
                    this.nowVideoStep = 2;

                })
                .catch(err => {
                    /*this.$toast.fail(err.message);*/
                })
        },
        /**/
        videoOnPause() {
            /**/
            this.videoStatus.isPlay = !this.videoStatus.isPlay;
            /**/
            /*this.showTopNavbar = true;*/
        },
        /**/
        videoOnClick(e) {
            // e.requestFullscreen();
            /**/
            this.videoStatus.isPlay = !this.videoStatus.isPlay;
            console.log('');
            console.log(this.videoStatus.isPlay);
            /**/
            /*this.showTopNavbar = false;*/
        },
        /*url*/
        returnRequestVideoType() {
            /*
            * return 0: 1:G 2:vip
            * */
            if (this.videoContentOptions.isVip === 1) {
                return 2;
            } else if (this.videoContentOptions.isVip === 0 && this.videoContentOptions.point === 0) {
                return 0;
            } else if (this.videoContentOptions.point > 0) {
                return 1;
            }
        },
        /**/
        getVideoUrl(type) {
            this.$requestApi.getVideoUrl({
                videoId: this.$route.params.id
            })
                .then(res => {
                    this.playerOptions.source = res.data.url;
                    /**/
                    this.isVideoAllFunctuonReady = true;
                    /*url*/
                    this.formatUrl = res.data.url;
                    /*()*/
                    /*this.nowVideoStep = 2;*/
                })
                .catch(err => {
                    console.log(err);
                });
            /*switch (type) {
                /!**!/
                case 0:
                    this.$requestApi.getFreeVideoUrl({
                        videoId: this.$route.params.id
                    })
                        .then(res => {
                            console.log("url")
                            console.log(res);
                            this.playerOptions.source = res.data.url;
                            /!**!/
                            this.isVideoAllFunctuonReady = true;
                            /!**!/
                            this.videoPlayEnoughFn();
                        })
                        .catch(err => {
                            console.log(err);
                        });
                    break;
                case 1:
                    /!**!/
                    this.$requestApi.getVideoUrl({
                        videoId: this.$route.params.id,
                    })
                        .then(res => {
                            console.log("url")
                            console.log(res);
                            this.playerOptions.source = res.data.url;
                            /!**!/
                            this.isVideoAllFunctuonReady = true;
                            /!**!/
                            this.videoPlayEnoughFn();
                        })
                        .catch(err => {
                            console.log(err);
                        });
                    break;
                case 2:
                    /!*vip*!/
                    this.$requestApi.getVideoUrl({
                        videoId: this.$route.params.id
                    })
                        .then(res => {
                            console.log("url")
                            console.log(res);
                            this.playerOptions.source = res.data.url;
                            /!**!/
                            this.isVideoAllFunctuonReady = true;
                            /!**!/
                            this.videoPlayEnoughFn();
                        })
                        .catch(err => {
                            console.log(err);
                        });
                    break;
            }*/
        },
        /**/
        clickCollectButton(type) {
            console.log("type");
            console.log(type);
            /*type0 1*/
            switch (type) {
                case 0:
                    this.$requestApi.userCancelCollectVideo({
                        videoIds: [this.$route.params.id]
                    })
                        .then(res => {
                            /**/
                            this.$toast('');
                            /**/
                            this.videoContentOptions.collection -= 1;
                            /**/
                            this.videoContentOptions.isCollected = 0;
                        })
                        .catch(err => {
                            /**/
                            this.$toast('');
                        })
                    break;
                case 1:
                    this.$requestApi.userCollectVideo({
                        videoId: this.$route.params.id
                    })
                        .then(res => {
                            /**/
                            this.$toast('');
                            /**/
                            this.videoContentOptions.collection += 1;
                            /**/
                            this.videoContentOptions.isCollected = 1;
                        })
                        .catch(err => {
                            /**/
                            this.$toast('');
                        })

                    break;
            }
        },
        /**/
        onClickLeft() {
            /*0*/
            this.$router.go(-1);
        },
        onClickRight() {

        },
        /**/
        getPreVideoUrl() {
            this.$requestApi.getTryVideoUrl({
                videoId: this.$route.params.id
            })
                .then(res => {
                    console.log("url")
                    console.log(res);
                    this.tryVideoUrl = res.data.preUrl;
                    /**/
                    this.videoStatus.tryWatchVideoIsReady = true;
                    /**/
                    this.videoStatus.isTryWatchVideo = true;
                    /**/
                    this.nowVideoStep = 3;
                    /*loading*/
                    this.$toast.clear();
                })
                .catch(err => {
                    console.log(err);
                });
        },
        /*aes*/
        getAESImgBase64Code(file) {
            this.$axios({
                url: file
            })
                .then(res => {
                    this.playerOptions.cover = this.$globalFn.decryptAESFn(res.data).replace(/\s*/g, "");
                    /*vipvip*/
                    if(this.$store.state.isLogin===true&&this.videoContentOptions.isVip===1&&this.$store.state.userInfo.isVip===0){
                        this.nowVideoStep=1;
                        return;
                    }
                    /*0*/
                    this.nowVideoStep = 0;
                    /*console.log('');
                    console.log(this.playerOptions.cover);*/
                })
                .catch(err => {

                })
        },
        /**/
        getAdFn(){
            this.$requestApi.getAdList({
                type:1
            })
            .then(res=>{
                for(let obj of res.data.list){
                    /*this.adOptions.bottom=obj;*/
                    if(obj.name==='h5video'){
                        this.adOptions.bottom=obj;
                        break;
                    }
                }
            })
            .catch(err=>{

            })
        },
        /**/
        getFirstData() {
            this.$requestApi.getVideoContent({
                videoId: this.$route.params.id
            })
                .then(res => {
                    let _this = this;
                    /*()*/
                    this.imgCoverOutside = res.data.info.coverImgUrl;
                    /*vip*/
                    this.videoContentOptions.isVip = res.data.info.isVip;
                    /*G*/
                    this.videoContentOptions.point = res.data.info.point;
                    /**/
                    this.videoContentOptions.isBuy = res.data.info.isBuy;
                    /**/
                    this.videoContentOptions.length = res.data.info.length;
                    /*async await*/
                    (async function () {
                        /*()*/
                        await _this.getAESImgBase64Code(res.data.info.coverImgUrl);
                        /**/
                        await _this.getVideoUrl(_this.returnRequestVideoType());
                    })();
                    /**/
                    this.videoContentOptions.tags = res.data.info.tags.split(',');
                    /**/
                    this.videoContentOptions.posterImgList = res.data.info.posterImgUrl;
                    /**/
                    this.videoContentOptions.createTime = res.data.info.addTime;
                    /**/
                    this.videoContentOptions.collection = res.data.info.collectionCount;
                    /**/
                    this.videoContentOptions.name = res.data.info.name;
                    /**/
                    this.videoContentOptions.playTimes = res.data.info.seeCount;
                    /**/
                    this.videoContentOptions.videoIntro = res.data.info.description === "" ? '' : res.data.info.description;
                    /**/
                    this.videoContentOptions.goodCount = res.data.info.likeCount;
                    /**/
                    /*this.$globalFn.addVideoHistory({
                        videoId: this.$route.params.id,
                        name: res.data.info.name,
                        active: false,
                        needVip: res.data.info.isVip,
                        addTime: this.$globalFn.getTimeStringForHistory(),
                        coverImgUrl: res.data.info.coverImgUrl
                    }, this.$store.state.videoHistoryList);*/
                    /**/
                    if (this.$store.state.isLogin === true) {
                        /**/
                        this.videoContentOptions.isGood = res.data.info.isLike;
                        /**/
                        this.videoContentOptions.isCollected = res.data.info.isCollect;
                        /*vipvip1*/
                        if(this.videoContentOptions.isVip===1&&this.$store.state.userInfo.isVip===0){
                            this.nowVideoStep=1;
                        }
                    }
                })
                .catch(err => {

                })
        },
        /**/
        resetVideoStatus() {
            /**/


            /*if (this.formatVideoObj !== null) {
                this.formatVideoObj.destroy(false);
            }
            if (this.tryVideoObj !== null) {
                this.tryVideoObj.destroy(false);
            }
            if (this.formatVideoCopyObj !== null) {
                this.formatVideoCopyObj.destroy(false);
            }*/
            /**/
            for (let key in this.videoStatus) {
                this.videoStatus[key] = false;
            }
            /**/
            this.isVideoAllFunctuonReady = false;
            /**/
            this.nowVideoStep = -1;
        }
    },
    data() {
        return {
            /**/
            adOptions:{
                /**/
                bottom:null
            },
            /*
            * 0:
            * 1.
            * 2.
            * 3.
            * */
            nowVideoStep: -1,
            /**/
            imgCoverOutside: "",
            /**/
            videoStatus: {
                /**/
                firstPlayIsStart: false,
                /**/
                isPlay: false,
                /**/
                isFullscreen: false,
                /**/
                isVideoPlayDone: false,
                /**/
                isTryWatchVideo: false,
                /**/
                tryWatchVideoIsPlay: false,
                /**/
                tryWatchVideoIsReady: false,
                /**/
                tryWatchVideoIsPlayDone: false
            },
            /**/
            showTopNavbar: true,
            /**/
            isVideoAllFunctuonReady: false,
            /**/
            videoContentOptions: {
                /**/
                name: "",
                /**/
                playTimes: 0,
                /**/
                createTime: "",
                /**/
                videoIntro: "",
                /**/
                goodCount: '',
                /**/
                collection: '',
                /**/
                download: false,
                /**/
                isGood: 0,
                /**/
                isCollected: 0,
                /*vip*/
                isVip: 0,
                /*G 00G*/
                point: 0,
                /**/
                isBuy: 0,
                /**/
                tags: [],
                /**/
                length: "",
                /**/
                posterImgList:[]
            },
            /**/
            collapseOptions: {
                active: []
            },
            /*video*/
            playerOptions: {
                /**/
                id: "",
                /**/
                source: "",
                /**/
                cover: "",
                /**/
                width: "100%",
                /**/
                /**/
                controlBarVisibility: 'always',
                /*centertopcenter*/
                x5_video_position: 'top',
                /*TBSlandscape  portraint*/
                x5_orientation: 'portraint',
                /*TBStrue*/
                x5LandscapeAsFullScreen: true,
                /**/
                autoplay: true,
                /*hls*/
                useHlsPluginForSafari: true
            },
            /**/
            formatVideoObj: null,
            /**/
            formatVideoCopyObj: null,
            /**/
            tryVideoObj: null,
            /**/
            formatUrl: null,
            /**/
            tryVideoUrl: null
        }
    },
    created() {
        /*0*/
        this.resetVideoStatus();
        this.getAdFn();
        /**/
        this.getFirstData();
    },
    mounted() {

    },
    beforeRouteLeave(to, from, next) {
        console.log(to);
        /*0*/
        this.resetVideoStatus();
        next();
    },
    watch: {
        '$route'(val) {
            /*0*/
            this.resetVideoStatus();
            /**/
            this.getFirstData();
        }
    }
}
</script>

