    padding: 10px 14px;
    font-size: 12px;
}

.beforeVideoStart {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.4);
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.fixedVideoBox {
    position: fixed;
    left: 0;
    top: 0;
}

.videoBigBg {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    position: relative;
}

.videoBigBg.fixed {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99998;
}

.videoSignBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
}

.videoSignBox > div.line {
    width: 100%;
    height: 1px;
    background-color: #ccc;
    margin-bottom: 4px;
}

.videoSignBox > ul > li > p {
    padding: 6px 12px;
    color: #A0A4AE;
