    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    /*padding: 10px;*/
}

.linkOutButton.try {
    background:linear-gradient(180deg,rgba(247,131,97,1) 0%,rgba(245,75,100,1) 100%);
}

.linkOutButton.vip {
    background:linear-gradient(180deg,rgba(247,131,97,1) 0%,rgba(245,75,100,1) 100%);
}

