<template>
    <div class="publicAllContentBg allIndex fullHeight" id="specialIndex">
        <!---->
        <publicTopHeader></publicTopHeader>
        <van-pull-refresh v-model="refreshLoadingStatus" @refresh="onRefresh">
            <!---->
            <div class="publicNeedXPaddingBg marginTop">
                <!--banner-->
                <!--<van-swipe class="indexSwiper" height="200" id="publicIndexBanner" indicator-color="white">
                    <van-swipe-item v-for="(item,index) in bannerOptions.list" :key="index">
                        <aesImage :file-url="item.imgUrl" :primordial-img="false" img-fit-type="fill"></aesImage>
                    </van-swipe-item>
                </van-swipe>-->
                <!---->
                <publicScrollSpecialList :video-list="specialListOptions.list"></publicScrollSpecialList>
                <!---->
                <artistScrollList :singer-list="singerListOptions.list"></artistScrollList>
            </div>
            <!---->
            <ul class="artistListBox">
                <li v-for="(item,index) in artistModuleOptions.list" :key="index">
                    <publicSpecialArtistModule
                        :moduleInfo="{id:item.id,name:item.name,intro:item.description,icon:item.headIco,videoCount:item.videoCount}"
                        :videoList="item.videos"></publicSpecialArtistModule>
                </li>
            </ul>
        </van-pull-refresh>
    </div>
</template>

<script>
import publicSpecialArtistModule from "../../components/publicSpecialArtistModule";
import artistScrollList from "../../components/artistScrollList";
import publicScrollSpecialList from "../../components/publicScrollSpecialList";
import aesImage from "../../components/aesImage";
import publicTopHeader from "../../components/publicTopHeader";

export default {
    name: "specialIndex",
    components: {
        publicTopHeader,
        aesImage,
        publicScrollSpecialList,
        artistScrollList,
        publicSpecialArtistModule
    },
    data() {
        return {
            /*loading*/
            refreshLoadingStatus: false,
            /*banner*/
            bannerOptions: {
                /**/
                list: []
            },
            /**/
            specialListOptions: {
                /**/
                list: []
            },
            /**/
            artistModuleOptions: {
                list: []
            },
            /**/
            singerListOptions: {
                /**/
                list: []
            },
        }
    },
    methods: {
        /**/
        onRefresh() {
            /**/
            this.specialListOptions.list = [];
            this.artistModuleOptions.list = [];
            this.singerListOptions.list = [];
            this.getSpecialList();
            this.getFamousSinger();
            this.getRecArtistList();
            this.refreshLoadingStatus=false;
        },
        /**/
        getRecArtistList() {
            this.$requestApi.getRecArtistList({
                page: 1,
                length: 5,
            })
                .then(res => {
                    /**/
                    this.artistModuleOptions.list = res.data.list;
                })
                .catch(err => {
                    console.log(err);
                })
        },
        /**/
        getFamousSinger() {
            this.$requestApi.getArtistList({
                page: 1,
                length: 5,
                type: 2
            })
                .then(res => {
                    console.log("");
                    console.log(res);
                    /*1*/
                    this.singerListOptions.list = res.data.list;
                })
                .catch(err => {
                    console.log(err);
                })
        },
        /**/
        getSpecialList() {
            this.$requestApi.getSpecialList({
                page: 1,
                length: 4,
            })
                .then(res => {
                    console.log("");
                    console.log(res);
                    /**/
                    this.specialListOptions.list = res.data.list;
                })
                .catch(err => {
                    console.log(err);
                })
        },
        /*banner*/
        getBannerFn() {
            /*banner*/
            this.$requestApi.getAdList({
                type: 1
            })
                .then(res => {
                    this.bannerOptions.list = res.data.list.filter(el => {
                        return el.name === "h5specia"
                    });
                })
                .catch(err => {

                });
        },
    },
    created() {
        /*this.getBannerFn();*/
        this.getSpecialList();
        this.getFamousSinger();
        this.getRecArtistList();
    }
}
</script>

<style scoped>
.artistListBox {
    margin-top: 20px;
}
.artistListBox>li{
    margin-top: 10px;
}
.artistListBox>li:first-child{
    margin-top: 0;
}
</style>



